import { useMutation, useSubscription } from "@apollo/react-hooks";
import { CircularProgress, Card, CardMedia, CardContent, Typography, CardActions, IconButton, makeStyles } from "@material-ui/core";
import { Pause, PlayArrow, Save } from "@material-ui/icons";
import React from "react";
import { SongContext } from "../App";
import { ADD_OR_REMOVE_FROM_QUEUE } from "../graphql/mutations";
import { GET_SONGS } from "../graphql/subscriptions";

function SongList() {
    const { data, loading, error } = useSubscription(GET_SONGS);
    // const song = {
    //     title: "Lune",
    //     artist: "Moon",
    //     thumbnail: "http://img.youtube.com/vi/--ZtUFsIgMk/0.jpg"
    // };

    if (loading) {
        return (
            <div style={{
                display: 'flex',
                flexDirection: 'column',
                alignItems: 'center',
                marginTop: 50
            }}>
                <CircularProgress></CircularProgress>
            </div>
        )
    }
    if (error) {console.log(error);
        return <div>Error fetching songs</div>
        
    }

    return (
    <div>
    {data.songs.map((song) => (
        <Song key={song.id} song={song} />
    ))}
    </div>
    );
}

const useStyles = makeStyles(theme => ({
    container: {
        margin: theme.spacing(3)
    },
    songInfoContainer: {
        display: 'flex',
        alignItems: 'center'
    },
    songInfo: {
        width: '100%',
        display: 'flex',
        justifyContent: 'space-between'
    },
    thumbnail: {
        objectFit: 'cover',
        width: 140,
        height: 140
    }
}))

function Song({ song }) {
    const { state, dispatch } = React.useContext(SongContext);
    const classes = useStyles()
    const { title, thumbnail, artist, id } = song;
    const [isCurrentSongPlaying, setIsCurrentSongPlaying] = React.useState(false);
    const [addOrRemoveFromQueue] = useMutation(ADD_OR_REMOVE_FROM_QUEUE, {
        onCompleted: data => {
            localStorage.setItem('queue', JSON.stringify(data.addOrRemoveFromQueue))
        }
    });
    React.useEffect(() => {
        const isSongPlaying = state.isPlaying && id === state.song.id;
        setIsCurrentSongPlaying(isSongPlaying);
    }, [id, state.song.id, state.isPlaying])

    function handleSongPlay() {
        dispatch({ type: "SET_SONG", payload: { song }})
        dispatch(state.isPlaying ? {type: "PAUSE_SONG"} : {type: "PLAY_SONG"})
    }

    function handleAddOrRemoveFromQueue() {
        addOrRemoveFromQueue({
            variables: { input: { ...song, __typename: "Song" }}
        });
        console.log("eheh");
    }

    return <Card className={classes.container}>
        <div className={classes.songInfoContainer}>
            <CardMedia image={thumbnail} className={classes.thumbnail}/>
            <div className={classes.songInfo}>
                <CardContent>
                    <Typography gutterBottom variant="h5" component="h2">
                        {title}
                    </Typography>
                    <Typography variant="body1" component="p" color="textSecondary">
                        {artist}
                    </Typography>
                </CardContent>
                <CardActions>
                    <IconButton onClick={handleSongPlay} size="small" color="primary">
                       {isCurrentSongPlaying ? <Pause/> : <PlayArrow/>}
                    </IconButton>
                    <IconButton onClick={handleAddOrRemoveFromQueue} size="small" color="secondary">
                        <Save/>
                    </IconButton>
                </CardActions>
            </div>
        </div>
    </Card>
    
    
}

export default SongList;